.calendar {
  flex-wrap: wrap;
  gap: 1px;
  width: 720px;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.calendar__day {
  background: #eee;
  border: 1px solid #000;
  width: 100px;
  height: 100px;
  transition: all .5s;
  position: relative;
}

.calendar__day:hover {
  cursor: pointer;
  background: #ffbfcb;
  transform: translateY(-20px);
}

.calendar__day:first-child:before {
  content: "1";
  font-family: Arial, sans-serif;
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

